// THIS FILE:  SECTIONS / STRUCTURE

  // HELPERS (type & color)
    // functional ("font-size-0") rather than bem style ("map__legend--small")
    // helpful for prototyping.  consider refactoring into scss %placeholder classes

  // ELEMENTS (type & color)
    // Custom Form Elements (checkbox & radio buttons)

  // COMPONENTS (blocks)
    // Naming convention
      // .some-block__some-element--some-modifier
      // .is_toggled_by_javascript

    // Selector Order
      // Order in which they first appear in the DOM

///////////////////////////////
// HELPERS
.font-size-0 {
  font-size: $font_size_0;
}
.font-size-1 {
  font-size: $font_size_1;
}
.font-size-2 {
  font-size: $font_size_2;
}
.font-size-3 {
  font-size: $font_size_3;
}

.italic {
  font-style: italic;
}

a,
input,
label,
.white-text {
  color: $white;
}
.red-text {
  color: $ui;
}

h1,h2,h3,h4,h5,h6,p,div,span,
.light-text {
  color: $light;
}

.gray-text {
  color: $gray;
}

.hide-on-mobile {
  @include mobile-only {
    display: none;
  }
}
.hide-on-desktop {
  @include desktop-only {
    display: none;
  }
}

.show-on-tablet {
  @media (min-width: $md_bp) {
    display: block;
  }
}



// END_HELPERS
///////////////////////////////

///////////////////////////////
// ELEMENTS
.button {
  @extend %button;
}

.button--close {
  @extend %button--close-panel;
}

.button--deselect {
  @extend %button--select;
}

.button--select {
  @extend %button--select;
}

.close-panel {

}

.inline-link {
  display: inline-block;
}

.inline-link.middle-link {

}

.text--slideout-primary {
  @extend %cp_primary;
}

.text--slideout-secondary {
  @extend %cp_secondary;
}

.text--slideout-tertiary {
  @extend %cp_tertiary;
}

.text--panel-label,
.text--link-label {
  @extend %ui_label;
  display: block;
}
.text--panel-label {
  color: $gray;
}

.text--link-heading {
  @extend %ui_dynamic;
  display: block;
}

.text--link-subheading {
  @extend %ui_tertiary;
  display: block;
}

.text--dynamic {
  @extend %ui_dynamic;
}

// FORM ELEMENTS
input,
label,
a,
button,
a span,
a div {
  cursor: pointer;
  text-decoration: none;
}

// input:hover,
// label:hover,
// button:hover,
// a:hover span,
// a:hover div {
//   text-decoration: underline;
// }

input[type="checkbox"],
input[type="radio"] {
  @extend %hide_input;
  // position & size will be set by specific classes
}
input[type="checkbox"] + label,
input[type="radio"] + label {
      @extend %label_text;
}

input[type="checkbox"] + label::before,
input[type="radio"] + label::before {
  @extend %before_label;
  // position & size will be set by specific classes
}

input[type="checkbox"] + label::after,
input[type="radio"] + label::after {
  @extend %after_label;
}

input[type="checkbox"]:checked + label,
input[type="radio"]:checked + label {
  @extend %label_text--checked;
}

input[type="checkbox"]:checked + label::after,
input[type="radio"]:checked + label::after {
    @extend %after_label--checked;
}

input[type="checkbox"]:hover + label::before,
input[type="radio"]:hover + label::before {
  @extend %before_label--hover;
}

// js listens for keyboard input and adds .show_keyboard_focus class
a.focus,
button.focus,
input.focus,
label.focus,
input.focus + label {
  box-shadow: none;
  outline: none;
}


a.show_keyboard_focus:focus,
button.show_keyboard_focus:focus {
  @extend %link--focus;
}
button:focus,
input.show_keyboard_focus[type="checkbox"]:focus + label.show_keyboard_focus::before,
input.show_keyboard_focus[type="radio"]:focus + label.show_keyboard_focus::before {
  @extend %before_label--focus;
}

input[type="radio"] + label:after {
  @extend %empty_radio;
}

input[type="radio"]:checked + label:after {
  @extend %radio_dot;
}

input[type="checkbox"] + label:after {
  @extend %empty_checkbox;
}

input[type="checkbox"]:checked + label:after {
  @extend %checkmark;
}

input[type="radio"] + label:hover:after,
input[type="checkbox"] + label:hover:after {
  @extend %pseudo_mark--hover;
}





// END_ELEMENTS
///////////////////////////////

