// COMPONENTS
.page-wrap {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

// All of the outermost items are absolutely positioned
.site-header {
  @include mobile-only {
    padding-left: $h_pad_mobile;
    padding-right: $h_pad_mobile;
    display: flex;
    flex-wrap: wrap;
  }

  @include desktop-only {
