      border: none;
      max-width: calcVW(12,$cols);
    }
    @include desktop-only {
      border-bottom: 2px solid $gray;
    }
  }
  .chart-header {
    padding-bottom: calcRem(1,$f1);
    @include mobile-only {
      order: 2;
      margin-top: calcRem($f2,1);
    }
  }
  .chart-header__heading {
    @extend %ui_primary;
    font-size: $font_size_2;
  }

  .chart-header__subheading {
    @extend %ui_primary;
    font-size: $font_size_1;
  }

  .chart-header__label {
    @extend %ui_tertiary;
    font-size: $font_size_1;
  }

  .fine-print-wrap {
    @extend %flex-wrap;
    @extend %flex-justify-between;
    @include mobile-only {
      width: 100%;
    }
    @include desktop-only {
      width: calcVW(3,$cols);
      margin-top: calcRem($f2,1);
      padding-bottom:  calcRem($f1,0.25); // to match up borders
    }

  }

  .fine-print {
    font-size: $font_size_0;
    line-height: $font_size_1;

    a {
      text-transform: uppercase;
    }
  }

  .toggle-control-panel {
    box-shadow: none;
    outline: none;
    overflow: visible;

    @include mobile-only {
      padding-right: calcRem($f1,4);
      position: absolute;
      top: 0;
      right: 0;
      .icon--from_left {
        display: none;
      }


    }

    @include desktop-only {
      display: inline-block;
      text-align: right;
      text-transform: none;
      margin-top: calcRem(1,$f1);
      width: calcVW(3,$cols);
      border-top: 2px solid $gray;
      padding-top: $font_size_1;


      padding-right: calcRem($f1,5);

      .icon--from_left {
        position: absolute;
        top: 7px;
        right: 1rem;
      }
    }

    .text--slideout-primary,
    .text--slideout-secondary {
      @media(max-width: 767px) {
        display: none;
      }
    }

    .icon--from_right {
      position: absolute;
      top: 7px;
      left: 0;
    }


  }
}

.control-panel {
  // see _animated.scss for positioning & size
  background-color: $black;
  padding: $v_pad_mobile $h_pad_mobile;

  @include desktop-only {
    background: $cp_gradient--large;
    padding: 1rem $cp_pad--large 10rem 0;
    top: 0;
  }


  .close-control-panel {
    @extend %button--close-panel;
    text-align: right;

    @media (min-width: $desktop_min) {
      top: $main_heading_top--mobile;
      right: $cp_pad--large;
      border-width: 0;
      padding: 0;
      text-transform: none;
    }

  }

  .close-control-panel:hover {
    border-color: $white;

  }

  .text--close-slideout {
    @extend %panel-button;
  }

  .control_panel__heading {
    font-size: $font_size_2;
    @extend %ui_tertiary;
    text-transform: uppercase;
    @include mobile-only {
      top: 4rem;
    }
    @include desktop-only {
      left: $h_pad_desktop;
      padding-top: calcRem($f2,1);
      padding-bottom: 2px; // to make the border line up
      margin-bottom: calcRem($f1,1);
    }
  }

  .control-panel__list {
    @include mobile-only {
      margin-top: $panel_top_space--mobile;
    }
  }
  .control-panel__item {
    padding-bottom: $font_size_1;
  }

  .control-panel__item__inner,
  .control-panel__link {
    width: 100%;
    display: block;
    border-top: 1px solid $dark;
    padding: $font_size_1 0 0;

    // special case
    @include desktop-only {
      .text--link-label,
      .text--link-heading,
      .text--link-subheading,
      .text--panel-label {
        left: $h_pad_desktop;
      }
    }

  }
  .control-panel__link:after {
    content: "";
    display: block;
    width: 0;
    position: absolute;
    top: 0;
    border-top: 1px solid $white;
  }

  .control-panel__link:hover {
    @include desktop-only {
      border-color: $black;
      text-decoration: none;
    }

  }

  .control-panel__link:hover:after {
    @include desktop-only {
      width: 100%;
      transition: width 200ms ease-in-out;
    }

  }

  .control-panel__link .icon {
    position: absolute;
    top: 6px;
    right: 0;
  }

  /* PROTOTYPE ONLY */
  .year-toggle-wrap {
    @extend %flex-wrap;
    @extend %flex-justify-between;
    @extend %flex-align-center;

    @include desktop-only {
      padding-left: $h_pad_desktop;
      max-width: 100%;
      overflow-x: hidden;
    }

    /* PROTOTYPE ONLY */
    .year-toggle-wrap__inner {
      text-align: center;
      width: 54px;

      @include desktop-only {
        .text--link-heading {
          left: 0;
        }
      }

    }
    /* PROTOTYPE ONLY */
    .year-toggle {
      border: 1px solid $white;
      display: inline-block;
      width: 20px;
      height: 20px;
      text-align: center;
      vertical-align: middle;
      margin: $font_size_0 2px;
      font-size: $font_size_1;
    }

    /* PROTOTYPE ONLY */
    .range-wrap {
      display: block;
      width: calc(100% - 144px);
      margin-left: 72px;
      margin-right: 72px;
      height: 32px;
      position: absolute;
      vertical-align: middle;

      @media (min-width: $desktop_min) {
        width: calc(100% - 182px)
      }

    }
    /* PROTOTYPE ONLY */
    .range-scale {
      width: 100%;
      height: 4px;
      background-color: $gray;
      position: absolute;
      left: 0;
      top: 14px;
    }

    /* PROTOTYPE ONLY */
    .range-marker {
      display: block;
      width: 100% * 14/15;
      border-left :2px solid $ui;
      border-right :2px solid $ui;
      height: 20px;
      position: absolute;
      left: 0;
      top: 6px; // 32-20
      padding-top: 6px;
      padding-bottom: 6px;
      vertical-align: middle;
    }
    /* PROTOTYPE ONLY */
    .range-highlight {
      background-color: $white;
      height: 100%;
      width: 100%;
      display: block;
    }
  }
  .control-panel__item-divider {
    border-bottom: 4px solid $dark;

    @media (min-width: $desktop_min) {
      border-width: 1px;
    }

  }

  .control-panel__series-toggle {
    @extend %flex-wrap;
    @extend %flex-justify-between;
    @extend %flex-align-center;
    width: 100%;
    padding: $font_size_1;

    @media (min-width: $desktop_min) {
      padding-left: $h_pad_desktop;
    }

  }

  .control-panel__toggle-option {
    width: 50%;
    display: block;
    height: 2.5rem;
  }



  .toggle-option__input {
    opacity: 0;
    border: 0;
    transition: opacity 0.2s ease-in-out;
  }

  .toggle-option__label {
    padding: 0.5rem;
    display: block;
    position: absolute;
    top: 0;
    width: 100%;
    text-align: center;
    &:hover {
      @include desktop-only {
        text-decoration: underline;
      }
    }
  }
  .toggle-option__label:before {
    border: 2px solid $gray;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .toggle-option--left,
  .toggle-option--left:before {
    border-radius: 8px 0 0 8px;
  }
  .toggle-option--right,
  .toggle-option--right:before {
    border-radius: 0 8px 8px 0;
  }

  .toggle-option__input:checked + .toggle-option__label:before {
    border-color: $white;
    color: $ui;
  }

  // don't put the dot in these sections
  input[type=radio]+.toggle-option__label:after,
  input[type=radio]:checked+ .toggle-option__label:after {
    display: none;
  }


  .control-panel__item__link-wrap {
    width: 100%;
    @media (min-width: $desktop_min) {
      padding-left: $h_pad_desktop;
    }
  }

  .inline-link {
    display: inline-block;
    margin-top: 16px;
    @extend %ui_dynamic;
  }
  .inline-link:hover {

    text-decoration: underline;
  }

  .middle-link {
    border-left: 1px solid $gray;
    border-right: 1px solid $gray;
    margin-left: 12px;
    margin-right: 12px;
    padding-left: 12px;
    padding-right: 12px;
  }



}

