.background-map {
  display: block;
  width: 100vw;
  height: 100vh;
  background-image: $main_background_image--mobile;
  background-position: right 30%;
  background-size: auto, cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;

  @media (min-width: $desktop_min) {
      background-image: $main_background_image--large;
  }

}
